
body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: black;
}
a, a:visited {
    color: white;
}
.action {
    display: inline-block;
    position: relative;
    margin-left: 5%;
    cursor:pointer;
}
ul {
    list-style: none;
    padding: 0px;
}
.flip-btn {
    position: absolute;
    bottom: 1%;
    right: 2%;
    text-align: right;
    cursor:pointer;
}
.flip-btn:after {
   content:'â†’'
 }
.flipped {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.stencil{
    position: relative;
    left: -20.4%;
    height: 240px;
    width: 400px;
}
.stencil-container {
    position: relative;
    top: 1%;
    border-radius: 3%;
    width: 250px;
    height: auto;
    overflow: hidden;
}
.flipper {
    -webkit-transition: 0.6s;
    -o-transition: 0.6s;
    transition: 0.6s;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
    position: relative;
}
.front, .back {
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
    -webkit-box-shadow: 0 0 3px 0 #F0F8FF;
            box-shadow: 0 0 3px 0 #F0F8FF;
	position: absolute;
    top: 0;
}
.flip-container{
    position: relative;
    margin: 0 auto;
    margin-top: 10%;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    padding: 1%;
    color: #F0F8FF;
	width: 320px;
    height: 500px;
}

.front {
    padding: 5%;
    z-index: 2;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    width: 320px;
	height: 500px;
}

.back {
    display: grid;
    grid-template-rows: auto 1fr auto;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    width: 320px;
    height: 500px;
    overflow-y: scroll;
}
.expand {
    width: 720px;
    height: 500px;
    margin-left: -75%;
} 
/* Smartphones (portrait and landscape) ----------- */
@media only screen
and (min-width : 360px)
and (max-width : 740px) 
and (orientation: portrait) {
    .expand {
        width: 320px;
        margin-left: 0;
    } 
    .flip-container {
        margin-top: 20%;
    }
}




