    .circle {
        font-size: 12px;
        -webkit-transition: font-size .75s;
        -o-transition: font-size .75s;
        transition: font-size .75s;
    }
    
    .view-focus {
        font-size: 15px;
    }
    
    .circle:before {
        margin-left: 2px;
        content: ' \25CF';
    }
    
    .indicator-container {
        /* background-attachment: scroll; */
        -webkit-transition: all 2s;
        -o-transition: all 2s;
        transition: all 2s;
        position: absolute;
        bottom: 0;
        left: 0;
        /* bottom: 0; */
        height: 25px;
        /* position: fixed; */
        text-align: center;
        width: 100%;
        background: black;
    }


/* Landscape */
@media screen 
and (min-width: 360px) 
and (max-height: 500px) 
and (orientation: landscape) { 

